<a class:active href={href} on:click|preventDefault={handleClick}>{text}</a>

<style>
a {
    color: white;
}

.active {
    font-weight: bold;
}
</style>

<script>
import { createEventDispatcher } from 'svelte';
export let href = '#';
export let text = '';
export let active = false;

let dispatch = createEventDispatcher();

function handleClick() {
    dispatch('activate', {
        text
    });
}
</script>